.popular
{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
    height:90vh;
}
.popular h1{
    margin-top:100px;
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.popular hr{
    width:200px;
    height:6px;
    border-radius: 10px;
    background-color: #252525;

}
.popular-item{
    margin-top:100px;
    display:flex;
    gap:30px;
}