.navbar
{
    display:flex;
    justify-content: space-around;
    padding:16px;
    box-shadow:0px 1px 3px -2px black;
    background-color:white;
}
.nav-logo
{
    display: flex;
    align-items: center;
    gap:10px;
}
.nav-logo img{
    width:300px;
}
.nav-menu
{
    display:flex;
    align-items:center;
    list-style:none;
    gap:50px;
    color: #626262;
    font-size:20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    gap :3px;
    cursor: pointer;
}
.nav-menu hr{
    border:none;
    width:85%;
    height:3px;
    border-radius: 10px;
    background-color:#FF7235;
}
.nav-login-cart
{
    display: flex;
    align-items: center;
    gap:45px;
}
.nav-login-cart button{
    width:157px;
    height:58px;
    outline:none;
    border:1px solid #FF7235;
    border-radius: 75px;
    color:white;
    font-size:20px;
    font-weight: 600;
    background-color: #FF7235;
    cursor:pointer;
}
.nav-login-cart button:active
{
    background-color: #f3f3f3;
}
.nav-cart-count{
    width:22px;
    height:22px;
    display:flex;
    justify-content: center;
    align-items:center;
    margin-top:-35px;
    margin-left:-55px;
    border-radius: 11px;
    font-size:14px;
    background-color:#FF7235;
    color:white;
}